/* Footer */

.footer {
    margin-top: auto;
    padding: 60px 0 50px;
    background-color: #407677; /* Нежно-фиолетовый фон */
    color: #6E3AAB; /* Фиолетовый текст */
}

.footer__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 27px;
}

.social {
    display: flex;
    column-gap: 30px;
    align-items: center;
}

.copyright {
    font-size: 16px;
    /* Добавьте здесь цвет текста и другие декоративные элементы, чтобы соответствовать женственной тематике */
}

.copyright p + p {
    margin-top: 0.5em;
}
